import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import {getWardSet,getDepartmentSet,getHospitalSet,updateWard, getWard} from './../Apis.js'
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';



class UpdateWard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name:[],
           first_name:'r',
            last_name:'',
            short_name:'',
            visible:false,
            email:'',
            role:'',
            phone:'',
            alternate_phone:'',
            gender:'',
            showForm:false,
            msgType:'success',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
        this.updateWard(values,setSubmitting,this.props.match.params.id)
       
    }

      updateWard=async(values,setSubmitting,id)=>{
          const responseData= await updateWard(values,id);
          console.log(responseData,'response')
          if(responseData.data.message)
          {
              this.setState({ visible:true,massege:'Ward Update Failed '+responseData.message,msgType:'danger'})
              setSubmitting(false);
          }
          else{
              this.setState({ visible:true,msgType:'success',massege:'Ward Update Successfully'})
              setSubmitting(false);
              return;
          }
      }

    
      componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 

            const responseDataDepart= await getDepartmentSet();
            console.log(responseDataDepart,'response')
            if(responseDataDepart.status == 200){
                this.setState({departmentSets:responseDataDepart.data.result})
            } else if (responseDataDepart.status == 404) {
                console.warn(responseDataDepart.data.message);
            } 
            const responseDataWard= await getWard(this.props.match.params.id);
            console.log(responseDataWard,'response')
            if(responseDataWard.status == 200){
                this.setState({
                
                    name:responseDataWard.data.name,
                    description:responseDataWard.data.description,
                    ward_no:responseDataWard.data.ward_no,
                    hospital_id:responseDataWard.data.hospital_id,
                    department_id:responseDataWard.data.department_id,
                    phone:responseDataWard.data.phone,
                    status:responseDataWard.data.status,
                    status_reason:responseDataWard.data.status_reason,
                    created_by:responseDataWard.data.created_by,
                    showForm:true,
                    
                })
            } else if (responseDataWard.status == 404) {
                console.warn(responseDataWard.data.message);
            } 
            

        }
    render() {
        console.log(this.state.visible)
       
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update Ward</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        name: this.state.name,
                                        description: this.state.description,
                                        ward_no: this.state.ward_no,
                                        hospital_id: this.state.hospital_id,
                                        department_id: this.state.department_id,
                                        phone: this.state.phone,
                                        status: this.state.status,
                                        status_reason: this.state.status_reason,
                                        modified_by: localStorage.getItem('id'),
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            // validationSchema={Yup.object().shape({
                            // email: Yup
                            //     .string()
                            //     .email()
                            //     .required(),
                            // password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // confirm_password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // phone:Yup
                            //     .number()
                            //     .required(),
                            // alternate_phone:Yup
                            //     .number()
                            //     .required(),
                            // })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                        <Form className="form-horizontal">
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="name" 
                                                    placeholder="Name" 
                                                    id="text-input"
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="first_name" />
                                                </Col>                                        
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Description:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="description" 
                                                    placeholder="Description" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="middle_name" />
                                                </Col>                                        
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Ward No:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="ward_no" 
                                                    placeholder="Ward No" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="last_name" />
                                                </Col>                                        
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Hospital Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                    
                                    <Field 
                                component="select" 
                                    name="hospital_id" 
                                    placeholder="Hospital Id" 
                                    className="form-control"
                                    >
                                    {this.state.hospitalSets&&this.state.hospitalSets.map((hospital, index) =>
                                    <option key={index} value={hospital.id}>{hospital.name}</option>
                                    )}
                                    </Field> 
                                    <ErrorMessage name="short_name" />
                                </Col>                                       
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Department Name:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    component="select" 
                                                    name="department_id" 
                                                    placeholder="Department Id" 
                                                    className="form-control"
                                                    >
                                                        {this.state.departmentSets&&this.state.departmentSets.map((department, index) =>
                                                        <option key={index} value={department.id}>{department.name}</option>
                                                    )}
                                                    </Field> 
                                                    {/* <ErrorMessage name="phone" /> */}
                                                </Col>                                  
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">                                                           
                                                    <Label htmlFor="text-input">Phone:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="phone" 
                                                    placeholder="Phone" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="alternate_phone" />
                                                </Col>                                        
                                            </FormGroup>
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Status:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    component="select" 
                                                    name="status" 
                                                    placeholder="Status" 
                                                    className="form-control"
                                                >
                                                    <option  value={1}>{'Enable'}</option>
                                                    <option  value={2}>{'Disable'}</option>
                                                </Field>
                                                    <ErrorMessage name="alternate_phone" />
                                                </Col>                                      
                                            </FormGroup>
                             
                                        
                                            <FormGroup row>
                                                <Col md="3">
                                                    <Label htmlFor="text-input">Status Reason:</Label>
                                                </Col>
                                                <Col xs="12" md="9">
                                                    <Field 
                                                    type="text" 
                                                    name="status_reason" 
                                                    placeholder="Status Reason" 
                                                    className="form-control"
                                                    /> 
                                                    <ErrorMessage name="confirm_password" />
                                                </Col>                                        
                                            </FormGroup>
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                                Update Ward
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateWard;